import React from "react";
import styled from "styled-components";

const BankingDetailModal = ({ className }) => {
  return (
    <div
      className={`${className} modal fade`}
      id="staticBackdrop"
      data-bs-backdrop="static"
      data-bs-keyboard="false"
      tabIndex="-1"
      aria-labelledby="staticBackdropLabel"
      aria-hidden="true"
    >
      <div className="modal-dialog">
        <div className="modal-content modal__custom">
          <div className="modal-header modal__custom__header">
            <button
              type="button"
              className="btn-close modal__custom__button--close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div className="modal-body">
            <p className="fs__details">
              Vui lòng chuyển khoản theo thông tin dưới đây để chúng tôi có thể kích hoạt bản báo
              cáo cho bạn.
            </p>
            <p className="fs__title mt-3">VIETCOMBANK</p>
            <p className="d-flex justify-content-between">
              Số tài khoản: <span>19030001119333</span>
            </p>
            <p className="d-flex justify-content-between">
              Số điện thoại: <span>0123456789</span>
            </p>
            <p className="d-flex justify-content-between">
              Tên đầy đủ: <span>Nguyễn Văn A</span>
            </p>
            <p className="d-flex justify-content-between">
              Số tiền: <span>499.000 VND</span>
            </p>
            <p
              className="fw-bolder border-top pt-3"
              style={{
                borderColor: "#ffc367 !important",
                borderWidth: "2px !important",
              }}
            >
              Lưu ý:
            </p>
            <p className="fs__details">
              Vui lòng kiểm tra lại thông tin trước khi chuyển để tránh sự cố đáng tiếc.
            </p>
          </div>
          <div className="modal-footer modal__custom__footer">
            <button type="button" className="btn button-secondary--outline" data-bs-dismiss="modal">
              Đóng
            </button>
            <button type="button" className="btn button-secondary">
              Xong
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default styled(BankingDetailModal)`
  .modal__custom {
    background-color: #fafafa;
    color: black;
    &__header,
    &__footer {
      border: 0;
    }
    &__button--close {
      color: #fafafa !important;
      font-weight: bolder;
      &:hover {
        color: $color-text-title;
      }
    }
  }
`;
