import React from "react";
import FormErrors from "../../common/components/FormErrors";
import BankingDetailModal from "./BankingDetailModal";
import FormStyleProvider from "./FormStyleProvider";

const Form3 = ({ register, errors, activeCodeError, isLoading }) => {
  return (
    <FormStyleProvider>
      <div className="step__form--general page--3 d-flex flex-column flex-lg-row">
        <div className="step__form--general--left">
          <p className="fs__title--main fw-bolder">Nhập mã kích hoạt</p>
          <div className="step__form__input">
            <input
              type="text"
              name="code"
              placeholder="Nhập mã kích hoạt"
              ref={register({ required: "Cần nhập mã kích hoạt" })}
              disabled={isLoading}
            />
            <FormErrors errors={errors} />
            <div className="error">{activeCodeError?.response?.data?.message}</div>
            <p className="fs__text fw-bolder mt-4">Lưu ý:</p>
            <p className="fs__details">
              Nếu bạn chưa có mã kích hoạt bạn cần đăng kí để nhận bài báo cáo.
            </p>
          </div>
        </div>
        <div className="step__form--general--right">
          <p className="fs__text fw-bolder">Đăng ký theo hướng dẫn</p>
          <div className="mt-5">
            <p className="fs__title--white fw-bolder">Cách 1</p>
            <p className="fs__text">Liên hệ người giới thiệu để nhận mã ưu đãi</p>
          </div>
          <div className="mt-3">
            <p className="fs__title--white fw-bolder">Cách 2</p>
            <p className="fs__text">
              Chat trực tiếp với hỗ trợ viên của ERA VIETNAM&nbsp;
              <a href="" className="fw-bolder" style={{ color: "#ffd597" }}>
                Tại đây
              </a>
            </p>
          </div>
          <div className="mt-3">
            <p className="fs__title--white fw-bolder">Cách 3</p>
            <p className="fs__text">
              Chuyển khoản để nhận mã kích hoạt&nbsp;
              <button
                className="button-text"
                data-bs-toggle="modal"
                data-bs-target="#staticBackdrop"
                style={{ color: "#ffd597" }}
              >
                Tại đây
              </button>
            </p>
            <BankingDetailModal />
          </div>
        </div>
      </div>
    </FormStyleProvider>
  );
};

export default Form3;
