import React from "react";
import styled from "styled-components";
const theme = {
  p: "50px",
};
const FormStyleProvider = ({ className, children }) => {
  return <div className={className}>{children}</div>;
};

export default styled(FormStyleProvider)`
  .step__form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__video {
      margin: 30px auto 50px;
      width: fit-content;
      background-color: gray;
    }
    &--icon {
      color: ${(props) => props.theme.colorTextTitle};
      > i {
        margin: 10px 20px 0;
        font-size: 42px;
        // width: 100px;
      }
    }
    &__button {
      -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
      -khtml-user-select: none; /* Konqueror HTML */
      -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
      user-select: none;
      display: flex;
      justify-content: space-between;
      > button {
        padding: 10px 15px;
        border: 0;
        font-weight: bolder;
        /* &:nth-child(1) {
          background-color: rgba(255, 255, 255, 0);
          &:hover {
            background-color: rgba(255, 255, 255, 0.385);
          }
        }
        &:nth-child(2) {
          color: #000;
          &:hover {
            background-color: #ffc267d7;
          }
        } */
      }
    }
    &--general {
      background-color: ${(props) => props.theme.colorCard};
      &--left {
        padding: ${theme.p};
        width: 60%;

        .step__form__input {
          width: 60%;
          display: flex;
          flex-direction: column;
          > input {
            margin: 10px 0;
            text-indent: 20px;
            background-color: #fafafa;
            color: ${(props) => props.theme.colorSecondary};
            height: 50px;
            border-radius: 8px;
            border: none;
            font-weight: bold;
            outline: none;
            &:focus {
              border: 3px solid ${(props) => props.theme.colorSecondary};
            }
          }
          input[type="date"] {
            text-indent: 10px;
            &::-webkit-calendar-picker-indicator {
              cursor: pointer;
              color: ${(props) => props.theme.colorSecondary};
              margin-right: 10px;
            }
          }
          input::-webkit-input-placeholder {
            color: ${(props) => props.theme.colorSecondary};
          }
        }
      }
      &--right {
        background-color: ${(props) => props.theme.colorSecondary};
        color: #fafafa;
        padding: ${theme.p};
        width: 40%;
        .button-text {
          background-color: rgba(255, 255, 255, 0);
          border: 0;
          color: ${(props) => props.theme.colorTextTitle};
          font-weight: bolder;
        }
      }
    }
    .modal__custom {
      background-color: #2b2b2bfa;
      &__header,
      &__footer {
        border: 0;
      }
      &__button--close {
        color: #fafafa !important;
        font-weight: bolder;
        &:hover {
          color: ${(props) => props.theme.colorTextTitle};
        }
      }
    }
    .disable {
      display: none !important;
    }
    .button-disable {
      visibility: hidden;
    }
    @media ${(props) => props.theme.breakPoints.lg} {
      &--general {
        &--right {
          width: 100%;
        }
        &--left {
          width: 100%;
        }
        .step__form__input {
          width: 100%;
        }
      }
    }
  }
`;
